body {
    margin: 0;
    padding: 0;
    font-family: Arial, sans-serif;
}

.menu {
    overflow: hidden;
    position: relative;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    white-space: nowrap;
    width: 100%;
    font-family: "Arial Narrow", sans-serif;
    background-color: #eee;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.menu-item {
    display: inline-block;
}

.menu-item_wide {
    display: block;
}

.menu-item_hidden {
    opacity: 0.5;
    position: absolute;
    pointer-events: none;
    z-index: -1;
}

.menu-item__link {
    color: #333;
    text-decoration: none;
    display: inline-block;
    padding: 10px;
}

.menu-item_wide .menu-item__link {
    display: block;
}

.menu-item__link:focus {
    background-color: #ddd;
}

.menu-item__link:hover,
.menu-item__link:active,
.menu-item__link_active,
.menu-item__link_active:focus {
    color: #fff;
    background-color: #3e7f1d;
}

.dropdown {
    font-family: "Arial Narrow", sans-serif;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    position: absolute;
    background: #eee;
}



